import React, { useState, useEffect } from "react";
import "./App.css";

const MIN_LEVEL = 0;
const MAX_LEVEL = 5;
const LEVEL_HEIGHT_PX = 20;
const FILL_LEVEL_DURATION_MS = 2000;
const Direction = {
  INCREASE: "INCREASE",
  DECREASE: "DECREASE",
};

function App() {
  const [level, setLevel] = useState(MIN_LEVEL);
  const [active, setActive] = useState(false);
  const [direction, setDirection] = useState(Direction.INCREASE);

  useEffect(() => {
    if (!active) {
      return;
    }

    if (direction === Direction.INCREASE) {
      if (level === MAX_LEVEL) {
        setActive(false);
        setDirection(Direction.DECREASE);
      } else {
        setTimeout(() => {
          setLevel(level + 1);
        }, FILL_LEVEL_DURATION_MS);
      }
    } else {
      if (level === MIN_LEVEL) {
        setActive(false);
        setDirection(Direction.INCREASE);
      } else {
        setTimeout(() => {
          setLevel(level - 1);
        }, FILL_LEVEL_DURATION_MS);
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [active, level]);

  const onBtnClick = () => {
    if (active) {
      return;
    }

    setActive(true);
  };

  let buttonText;

  if (active) {
    buttonText = `${
      direction === Direction.INCREASE ? "Increasing" : "Decreasing"
    } water level...`;
  } else {
    buttonText = `${
      direction === Direction.INCREASE ? "Increase" : "Decrease"
    } water level`;
  }

  return (
    <div className="wrapper">
      <div className="bathtub-wrapper">
        <div className="bathtub">
          {Array.from(Array(level).keys()).map((i) => (
            <div key={i} className="water-level" />
          ))}
        </div>
      </div>

      <div className="btn-wrapper">
        <button className="btn" onClick={onBtnClick} disabled={active}>
          {buttonText}
        </button>
      </div>

      <div className="counter">Height: {level * LEVEL_HEIGHT_PX}</div>
    </div>
  );
}

export default App;
